.clients {
  z-index: 1;
  background-image: url("bcg_clients.455ac4ee.png");
  background-size: cover;
  position: relative;
}

.clients__content {
  flex-direction: column;
  display: flex;
}

.clients__title {
  height: 55px !important;
}

.clients__subtitle {
  margin-bottom: 16px !important;
}

.clients__title, .clients__subtitle, .clients__separator {
  z-index: 3;
  position: relative;
}

.clients:after {
  content: " ";
  z-index: 2;
  background-color: #fffc;
  position: absolute;
  inset: 0;
}

.clients__card {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 16px 0;
  display: flex;
}

.clients__card__img {
  z-index: 3;
}

.clients__card__name {
  color: #2e3033;
  text-transform: uppercase;
  z-index: 3;
  justify-content: center;
  align-items: center;
  height: 36px;
  margin: 20px 0 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  position: relative;
}

.clients__card__position {
  color: #2e3033;
  text-transform: uppercase;
  z-index: 3;
  justify-content: center;
  align-items: center;
  height: 14px;
  margin: 0 0 20px;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  position: relative;
}

.clients__card__text {
  z-index: 3;
  position: relative;
  height: 102px !important;
}

.clients__card__separator {
  z-index: 3;
  position: relative;
  margin-bottom: 22px !important;
}

.clients__card:last-of-type {
  padding-bottom: 30px;
}

@media (width >= 768px) {
  .clients {
    background-image: url("bcg_clients.276cb475.png");
    padding: 28px 0;
  }

  .clients .clients__content {
    flex-flow: wrap;
    justify-content: center;
  }

  .clients .clients__content .clients__card {
    -moz-column-gap: 48px;
    flex-direction: row;
    align-items: flex-start;
    column-gap: 48px;
    max-width: 454px;
    padding: 30px 25px 0;
    display: flex;
  }

  .clients .clients__content .clients__card__img {
    margin-top: 14px;
    display: flex;
  }

  .clients .clients__content .clients__card__separator {
    width: 48px;
    margin: 0;
  }

  .clients .clients__content .clients__card__name, .clients .clients__content .clients__card__position {
    justify-content: flex-start;
  }

  .clients .clients__content .clients__card__text {
    text-align: left;
  }

  .clients__card:last-of-type {
    padding-bottom: 0;
  }
}

@media (width >= 1300px) {
  .clients {
    padding: 42px 0;
  }

  .clients .clients__content {
    justify-content: center;
    max-width: 1510px;
    margin: 0 auto;
  }

  .clients .clients__card {
    height: 285px;
    max-width: 550px !important;
  }

  .clients .clients__card__img {
    width: 130px;
    height: 130px;
  }

  .clients .clients__card__separator {
    width: 70px;
    margin: 0 0 32px !important;
  }
}
